import PropTypes from 'prop-types';

import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';

import Scrollbar from '../Scrollbar';

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'brand', label: 'Brand', alignRight: false },
  { id: 'availableQuantity', label: 'Available Quantity', alignRight: false },
  { id: 'value', label: 'Value ($)', alignRight: false },
];

BonusListTable.propTypes = {
  bonusList: PropTypes.array,
};

function BonusListTable({ bonusList = [] }) {
  return (
    <Scrollbar>
      <TableContainer sx={{ minWidth: 800 }}>
        <Table aria-label="bonus table">
          <TableHead>
            <TableRow>
              {TABLE_HEAD.map((headCell) => (
                <TableCell key={headCell.id} align={headCell.alignRight ? 'right' : 'left'}>
                  {headCell.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {bonusList.map((row) => (
              <TableRow key={row.name}>
                <TableCell>{row.name}</TableCell>
                <TableCell>{row.brand}</TableCell>
                <TableCell>{row.availableQuantity}</TableCell>
                <TableCell>{row.value}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Scrollbar>
  );
}

export default BonusListTable;
